@font-face {
  font-family: "OpenSans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "OpenSans";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page__body {
  font-family: "OpenSans", sans-serif;
  font-weight: 400;
  margin: 0;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  min-width: 320px;
  font-size: 17px;
  line-height: 24px;
  background-color: #ffffff;
}
.main-header {
  width: 100%;
}
.main-header__wrapper {
  position: relative;
  display: grid;
  grid-template-columns: 240px 20px;
  grid-template-rows: 79px 1fr 1fr;
  width: 320px;
  padding: 0 30px;
  box-sizing: border-box;
  margin: 0 auto;
  align-items: center;
  justify-content: space-between;
}
.main-header__logo {
  position: absolute;
  left: 117px;
  top: 1px;
  width: 123px;
  display: flex;
  align-items: center;
  margin: 0 auto;
  height: 79px;
}
.main-header__logo:hover {
  opacity: 0.6;
}
.main-header__logo:active {
  opacity: 0.3;
}
.main-header__logo:focus {
  opacity: 0.6;
  outline: none;
}
.main-header__prompt {
  display: none;
}
.main-header__toggle {
  position: absolute;
  top: 27px;
  right: 28px;
  z-index: 2;
  width: 21px;
  height: 20px;
  cursor: pointer;
  border: none;
  background-image: url("../img/main-header/menu.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.main-header--closed .main-header__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  background-image: url("../img/main-header/menu.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.main-header--closed .main-header__toggle {
  top: 32px;
  right: 31px;
  left: 0;
  background-image: url("../img/main-header/menu-close.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.main-header--closed .main-header__toggle::before,
.main-header--closed .main-header__toggle::after {
  content: "";
  position: absolute;
  top: 24px;
  left: 28px;
  background-image: url("../img/main-header/menu-close.svg");
  background-repeat: no-repeat;
  background-position: center;
}
@media (min-width: 768px) {
  .main-header {
    width: 100%;
  }
  .main-header__wrapper {
    position: relative;
    display: grid;
    grid-template-rows: repeat(2, 88px);
    grid-template-columns: 400px 269px;
    width: 768px;
    margin: 0 auto;
  }
  .main-header__logo {
    position: initial;
    display: flex;
    grid-column: 1;
    grid-row: 1;
    padding: 0 0 0 24px;
    align-items: center;
    margin: 0;
  }
  .main-header__border {
    position: absolute;
    top: 87px;
    height: 1px;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.3);
  }
  .main-header__prompt {
    grid-column: 2;
    grid-row: 2;
    display: block;
    font-size: 15px;
    font-weight: 400;
    line-height: 24px;
    color: #444444;
    margin: 0 0 27px 24px;
  }
  .main-header__toggle {
    display: none;
  }
}
@media (min-width: 1150px) {
